.scale(@scaleX, @scaley) {
  -webkit-transform: scale(@scaleX, @scaley);
  -moz-transform: scale(@scaleX, @scaley);
  -ms-transform: scale(@scaleX, @scaley);
  transform: scale(@scaleX, @scaley);
}
.move(@scalex, @scaley, @translatex, @translatey) {
  -webkit-transform: scale(@scalex, @scaley) translate(@translatex, @translatey);
  -moz-transform: scale(@scalex, @scaley) translate(@translatex, @translatey);
  -ms-transform: scale(@scalex, @scaley) translate(@translatex, @translatey);
  transform: scale(@scalex, @scaley) translate(@translatex, @translatey);
}
.transform-origin(@tr-position){
  -webkit-transform-origin: @tr-position;
  -moz-transform-origin: @tr-position;
  -ms-transform-origin: @tr-position;
  transform-origin: @tr-position;
}
.animation-fill-mode(@anim-mode) {
  -webkit-animation-fill-mode: @anim-mode;
  -moz-animation-fill-mode: @anim-mode;
  -ms-animation-fill-mode: @anim-mode;
  animation-fill-mode: @anim-mode;
}
.flip-outx(@pers, @deg){
  -webkit-transform: perspective(@pers) rotateX(@deg);
  transform: perspective(@pers) rotateX(@deg);
}
.open > .dropdown-menu {
  // .move(1, 1, 0, 0);
  .flip-outx(800px, 0deg);
}
.dropdown-menu {
  .transform-origin(top);
  .animation-fill-mode(forwards);
  // .scale(1, 0);
  // .move(1, 0, 0, 0);
  .flip-outx(800px, -90deg);
  .transition(all 0.2s ease-out);
  display: block;
}
.dropup .dropdown-menu {
  .transform-origin(bottom);
  // .move(1,0, 0, -12px)
  .flip-outx(800px, 90deg);
}
.open.dropup .dropdown-menu {
  // .move(1, 1, 0, -12px)
  .flip-outx(800px, 0deg);
}

