@bg-dark-color: #323538;
.dropup > .dropdown-menu:after {
  border-bottom: 0;
  border-top: 6px solid @bg-dark-color;
  top: auto;
  display: inline-block;
  bottom: -7px;
  content: '';
  position: absolute;
  left: 50%;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
}